@use 'abstracts/mixins' as *;

body {
  background: var(--white);
}

.jumbotron {
  padding-top: 134px;

  @include md {
    padding-top: 76px;
  }

  .section-title {
    color: var(--mid-green);
    font-size: 20px;
    margin-bottom: 24px;
    padding-left: 0;
    margin-left: -1px;
    letter-spacing: 2px;

    &::before {
      font-size: 20px;
      margin-right: 4px;

      @include md {
        font-size: 16px;
      }
    }

    @include md {
      font-size: 16px;
      margin-bottom: 22px;
      letter-spacing: 1.6px;
    }
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 30px;
    letter-spacing: 1.5px;
    line-height: 46px;
    color: var(--gold);
    font-weight: 300;
    margin-bottom: 65px;
    max-width: 940px;
    margin-left: -2px;

    @include md {
      font-size: 20px;
      letter-spacing: 1px;
      line-height: 28px;
      padding-left: 2px;
      padding-right: 30px;
      margin-bottom: 38px;
    }

    span {
      color: var(--gold);
    }
  }
}

.white-paper {
  &__img-top {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 734px;
    margin-bottom: 68px;

    @include md {
      min-height: 269px;
      background-size: cover;
      background-repeat: no-repeat;
      margin-bottom: 49px;
      background-attachment: scroll !important;
      background-position: center !important;
    }

    &.maisie {
      background-image: url('./../assets/maisie_mainimage_1.webp');
    }

    &.kala-cotton {
      background-image: url('./../assets/kalacotton-main.webp');
    }

    &.circular-procurement {
      background-image: url('./../assets/cp-main.webp');
    }

    &.circular-product {
      background-image: url('./../assets/faribank-mainimage.webp');
    }

    &.gap-report {
      background-image: url('./../assets/report_maininage.webp');
    }

    &.john-cotton {
      background-image: url('./../assets/cotton-mainimage.webp');
    }
  }

  .container {
    padding-left: 154px;

    @include md {
      padding-left: 23px;
    }
  }

  &-mg-top-60 {
    margin-top: 61px;
    @include md {
      margin-top: 34px;
    }
  }

  &-mg-top-70 {
    margin-top: 73px;

    @include md {
      margin-top: 36px;
    }
  }

  &__parallax-img {
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 757px;
    margin-bottom: 77px;
    margin-top: 77px;

    &-maise-2 {
      background-image: url('../assets/maisie_image_3.webp');
    }
    &-maise-3 {
      background-image: url('../assets//maisie_image_5.webp');
    }
    &-kala-cotton-1 {
      background-image: url('../assets/kalacotton-2nd.webp');
    }
    &-john-cotton-1 {
      background-image: url('../assets/cotton-image-3.webp');
      background-size: cover !important;
      background-position: center !important;
    }

    @include md {
      margin-top: 57px;
      margin-bottom: 38px;
      min-height: 399px;
      background-attachment: scroll !important;
      background-position: center !important;
    }
  }

  &__text {
    color: var(--dark-grey);
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.5px;
    margin-bottom: 28px;
    font-family: var(--unnamed-font-family-garamond-premier-pro);

    @include md {
      font-size: 16px;
      letter-spacing: 0.4px;
      line-height: 22px;
      margin-bottom: 22px;
    }

    &--left-padding {
      margin-left: 193px;
      max-width: 640px;

      @include md {
        margin-left: 40px;
      }
    }

    &--max-width-710 {
      max-width: 708px;
    }
  }

  &__img {
    img {
      max-width: 100%;
      display: block;
      height: auto;
    }
  }

  &__list {
    padding-left: 90px;
    max-width: 790px;

    li {
      color: var(--dark-grey);
      font-size: 20px;
      line-height: 28px;
      letter-spacing: 0.5px;
      margin-bottom: 28px;
      font-family: var(--unnamed-font-family-garamond-premier-pro);
      position: relative;

      &::before {
        content: '•';
        font: normal normal 300 20px/28px Garamond Premier Pro;
        letter-spacing: 0.5px;
        color: var(--gold);
        position: absolute;
        left: -30px;
      }

      @include md {
        font-size: 16px;
        letter-spacing: 0.4px;
        line-height: 22px;
        margin-bottom: 22px;
      }
    }
  }

  &__title {
    font-size: 28px;
    letter-spacing: 2.8px;
    line-height: 38px;
    margin-bottom: 36px;
    color: var(--green-light);
    text-transform: uppercase;

    @include md {
      font-size: 16px;
      letter-spacing: 1.6px;
      line-height: 24px;
      margin-bottom: 16px;
    }
  }

  &__subtitle {
    font-size: 20px;
    letter-spacing: 2px;
    line-height: 38px;
    margin-bottom: 36px;
    color: var(--gold);
    text-transform: uppercase;

    @include md {
      font-size: 14px;
      letter-spacing: 1.6px;
      line-height: 24px;
      margin-bottom: 16px;
    }
  }

  &__text-box,
  &__steps {
    padding-left: 193px;
    position: relative;

    @include md {
      padding-left: 23px;
    }

    &::before {
      content: '';
      display: block;
      height: 100%;
      width: 2px;
      background: var(--gold);
      top: 0;
      position: absolute;
      left: 175px;

      @include md {
        left: 40px;
      }
    }
  }

  &__text-box {
    @include md {
      padding-left: 53px;
    }
    .white-paper__text {
      max-width: 708px;
    }
  }

  &__steps {
    margin-top: 73px;
    margin-bottom: 108px;

    @include md {
      margin-top: 51px;
      padding-left: 53px;
      margin-bottom: 60px;
    }

    &__title {
      text-transform: uppercase;
      margin-bottom: 17px;
      font-size: 20px;
      line-height: 34px;
      letter-spacing: 2px;
      color: var(--gold);
      max-width: 708px;

      @include md {
        font-size: 13px;
        line-height: 24px;
        margin-bottom: 7px;
        letter-spacing: 1.3px;
      }
    }

    .white-paper__text {
      max-width: 708px;
      margin-bottom: 46px;

      @include md {
        margin-bottom: 36px;
      }
    }
  }

  &__quote {
    max-width: 756px;
    margin-left: 65px;
    margin-top: 95px;
    margin-bottom: 74px;

    @include md {
      margin-top: 35px;
      margin-left: 0;
      margin-bottom: 56px;
    }

    &__text {
      &::before {
        content: '';
        background: url('./../assets/quote.svg') no-repeat;
        background-size: contain;
        display: block;
        margin: 0 auto 25px auto;
        width: 105px;
        height: 97px;

        @include md {
          width: 45px;
          height: 43px;
          margin-bottom: 23px;
        }
      }

      font-size: 28px;
      letter-spacing: 2.8px;
      line-height: 50px;
      margin-bottom: 30px;
      color: var(--mid-green);
      text-transform: uppercase;
      text-align: center;

      @include md {
        font-size: 16px;
        letter-spacing: 1.6px;
        line-height: 24px;
        margin-bottom: 19px;
      }
    }

    &__author {
      text-align: center;
      color: var(--dark-grey);
      font-size: 20px;
      line-height: 28px;
      letter-spacing: 0.5px;
      margin-bottom: 28px;
      font-family: var(--unnamed-font-family-garamond-premier-pro);
      max-width: 366px;
      margin: 0 auto;

      @include md {
        font-size: 16px;
        letter-spacing: 0.4px;
        line-height: 22px;
        max-width: 150px;
      }
    }
  }

  &__button-container {
    max-width: 756px;
    margin-bottom: 74px;
    margin-left: 65px;
    display: flex;
    justify-content: center;

    a,
    button {
      max-width: 340px;
    }
  }

  &__img-with-text {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 68px;
    margin-top: 56px;

    @include md {
      flex-wrap: wrap;
      margin-top: 29px;
      margin-bottom: 47px;
    }

    &__img {
      max-height: 584px;

      @include md {
        height: auto;
        order: 1;
        width: 100%;
        padding-left: 64px;
        margin-bottom: 25px;
      }

      img {
        max-width: 100%;
        max-height: 584px;
        height: auto;
        display: block;
      }
    }

    &__text {
      padding-right: 37px;
      display: flex;
      align-items: flex-end;
      color: var(--mid-green);
      font-size: 20px;
      line-height: 26px;
      font-family: 'mr-eaves-modern', sans-serif;
      font-weight: 200;
      text-align: right;
      max-width: 276px;

      @include md {
        order: 2;
        padding-right: 23px;
      }
    }
  }

  &__bottom {
    margin-top: 65px;

    @include md {
      margin-top: 35px;
    }

    &__share {
      padding-left: 193px;
      display: flex;
      align-items: center;

      @include md {
        padding-left: 53px;
      }

      &__title {
        text-transform: uppercase;
        color: var(--gold);
        font-weight: 700;
        font-size: 16px;
        letter-spacing: 1.6px;
        margin-right: 15px;
      }

      &__links {
        display: flex;

        li {
          flex-shrink: 0;
          margin-right: 8px;

          a {
            width: 33px;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 33px;
            border: 2px solid var(--mid-green);
            border-radius: 50%;

            &:hover {
              background: var(--gold);
              border-color: var(--gold);

              path,
              rect {
                fill: var(--mid-green);
              }
            }

            svg {
              height: 15px;
            }

            path,
            rect {
              fill: var(--gold);
            }
          }
        }
      }
    }
  }
}

.read-more {
  padding-top: 85px;

  @include md {
    padding-top: 51px;
  }

  &-container {
    display: flex;
    flex-wrap: wrap;
    padding: 0 37px;

    @include md {
      padding: 0;
    }
  }

  &__item {
    width: 50%;
    padding: 0 22px;
    margin-bottom: 83px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-self: stretch;

    @include md {
      width: 100%;
      padding: 0 0 0 15px;
      margin-bottom: 44px;
    }

    &::before {
      content: '';
      display: block;
      height: 100%;
      left: 0;
      top: 0;
      width: 2px;
      background: var(--gold);
      position: absolute;
    }

    &__img {
      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &__title {
      text-transform: uppercase;
      color: var(--mid-green);
      font-size: 22px;
      letter-spacing: 2.2px;
      line-height: 38px;
      margin-top: 34px;
      margin-bottom: 58px;

      @include md {
        margin-top: 23px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 1.6px;
        margin-bottom: 33px;
      }
    }

    &__button {
      &-container {
        display: flex;
        align-items: flex-end;
        flex-grow: 1;
        max-width: 235px;

        @include md {
          max-width: 151px;
        }

        .button-outlined {
          @include md {
            padding-left: 11px;
            font-size: 10px;
            letter-spacing: 1px;
            height: 39px;
          }
        }
      }
    }
  }

  &__button-container {
    margin: 17px auto 84px auto;
    max-width: 340px;
    width: 100%;

    @include md {
      max-width: 251px;
      margin-top: 21px;
      margin-bottom: 66px;
    }

    .button-outlined {
      @include md {
        padding-left: 11px;
        font-size: 10px;
        letter-spacing: 1px;
        height: 39px;
      }
    }
  }
}
